/* eslint-disable */
import { move } from 'react-big-calendar'
import React from 'react'
import '../../styles/components/calendar/ToolBar.css'

export default function ToolBar(props) {
	const toDay = () => props.onView('day')
	const toWeek = () => props.onView('week')
	const toMonth = () => props.onView('month')
	const toAgenda = () => props.onView('agenda')
	const toToday = () => props.onNavigate('TODAY')
	const toNext = () => props.onNavigate('NEXT')
	const toPrev = () => props.onNavigate('PREV')

	return (
		<div className="toolbar__container">
			<div className="toolbar__group1">
				<span onClick={toPrev} className="toolbar__arrow --arrowleft">
					<i className="fas fa-arrow-left"></i>
				</span>
				<button onClick={toToday} className="toolbar__button --todaybtn">
					{props.localizer.messages.today}
				</button>
				<span onClick={toNext} className="toolbar__arrow --arrowright">
					<i className="fas fa-arrow-right"></i>
				</span>
			</div>

			<span>{props.label}</span>

			<div className="toolbar__group">
				<button onClick={toMonth} className="toolbar__view toolbar__button">
					{props.views[0]}
				</button>
				<button onClick={toWeek} className="toolbar__view toolbar__button">
					{props.views[1]}
				</button>
				<button onClick={toDay} className="toolbar__view toolbar__button">
					{props.views[2]}
				</button>
				<button onClick={toAgenda} className="toolbar__view toolbar__button">
					{props.views[3]}
				</button>
			</div>
		</div>
	)
}
