import { types } from '../types'
import { stringFormatDateToDate } from '../../helpers/stringFormatDateToDate'
import EventsService from '../../api/events'

import { setErrorAction } from './uiActions'

export const startLoadEvents = () => {
	return dispatch =>
		EventsService.getAllEvents()
			.then(({ data }) => {
				const eventsStringDateToDate = stringFormatDateToDate(data.events)
				dispatch(loadEvents(eventsStringDateToDate))
			})
			.catch(() =>
				dispatch(
					setErrorAction('Failed to load events, contact an administrator')
				)
			)
}

export const loadEvents = events => ({
	type: types.loadEvents,
	payload: {
		events
	}
})

export const setActiveEventAction = event => ({
	type: types.eventSetActive,
	payload: {
		event
	}
})

export const startAddNewEventAction = event => {
	return dispatch => {
		const { title, notes, startDate, endDate } = event
		EventsService.createEvent(title, notes, startDate, endDate)
			.then(res => {
				const eventStringDateToDate = stringFormatDateToDate(res.data.newEvent)
				dispatch(addNewEventAction(eventStringDateToDate))
			})
			.catch(err => err)
	}
}

export const addNewEventAction = event => ({
	type: types.eventAddNew,
	payload: {
		event
	}
})

export const editEventAction = event => ({
	type: types.eventEdit,
	payload: {
		event
	}
})

export const startDeleteEventAction = eventId => {
	return dispatch => {
		EventsService.deleteEvent(eventId)
			.then(res => res.data.ok && dispatch(deleteEventAction(eventId)))
			.catch(e => e)
	}
}

export const deleteEventAction = eventId => ({
	type: types.eventDelete,
	payload: {
		eventId
	}
})
