import { useDispatch, useSelector } from 'react-redux'
import Modal from 'react-modal'
import DateTimePicker from 'react-datetime-picker'
import React, { useEffect, useState } from 'react'
import '../../styles/components/calendar/CalendarModal.css'
import moment from 'moment'

import { closeModalAction } from '../../redux/actions/uiActions'
import {
	editEventAction,
	setActiveEventAction,
	startAddNewEventAction
} from '../../redux/actions/eventsActions'

const now = moment().minutes(0).seconds(0).add(1, 'hours') // 3:00:00
const nowPlus1 = now.clone().add(1, 'hours')

const initFormValues = {
	title: '',
	notes: '',
	startDate: now.toDate(),
	endDate: nowPlus1.toDate()
}

export default function CalendarModal() {
	const state = useSelector(state => state)
	const { isModalOpen } = state.ui.modal
	const { activeEvent } = state.events
	const dispatch = useDispatch()

	const [formValues, setFormValues] = useState(initFormValues)

	const { notes, title } = formValues
	const handleInputChange = ({ target }) =>
		setFormValues(values => ({
			...values,
			[target.name]: target.value
		}))

	const handleSubmitForm = e => {
		e.preventDefault()
		if (activeEvent) {
			dispatch(
				editEventAction({
					...formValues
				})
			)
		} else {
			dispatch(startAddNewEventAction(formValues))
		}
		dispatch(closeModalAction())
		dispatch(setActiveEventAction(null))
		setFormValues(initFormValues)
	}

	const startDateChangeHanlder = e => {
		setFormValues(values => ({
			...values,
			startDate: e
		}))
	}

	const endDateChangeHanlder = e => {
		setFormValues(values => ({
			...values,
			endDate: e
		}))
	}

	Modal.setAppElement('#root')
	const closeModal = () => {
		dispatch(closeModalAction())
		dispatch(setActiveEventAction(null))
		setFormValues(initFormValues)
	}

	useEffect(() => {
		activeEvent ? setFormValues(activeEvent) : setFormValues(initFormValues)
	}, [activeEvent, setFormValues])

	return (
		<Modal
			isOpen={isModalOpen}
			onRequestClose={closeModal}
			contentLabel="Example Modal"
			closeTimeoutMS={200}
			className="calendarmodal"
			overlayClassName="modal-fondo"
		>
			<div className="modalHeader">
				<h1>{activeEvent ? 'Edit Event' : 'New Event'} </h1>
				<span
					onClick={() => dispatch(closeModalAction())}
					className="modalClose"
				>
					<i className="fas fa-times-circle fa-2x"></i>
				</span>
			</div>
			<hr />
			<form className="container" onSubmit={handleSubmitForm}>
				<div className="form-group">
					<label>Start date and time</label>
					<DateTimePicker
						onChange={startDateChangeHanlder}
						value={formValues.startDate}
						className="form-control"
					/>
				</div>

				<div className="form-group">
					<label>End date and time</label>
					<DateTimePicker
						onChange={endDateChangeHanlder}
						value={formValues.endDate}
						minDate={formValues.startDate}
						className="form-control"
					/>
				</div>

				<hr />
				<div className="form-group">
					<label>Title and Notes</label>
					<input
						type="text"
						className="form-control"
						placeholder="Event title"
						name="title"
						autoComplete="off"
						value={title}
						onChange={handleInputChange}
						required
					/>
					<small id="emailHelp" className="form-text text-muted">
						A short description
					</small>
				</div>

				<div className="form-group">
					<textarea
						type="text"
						className="form-control"
						placeholder="Notes"
						rows="5"
						name="notes"
						value={notes}
						onChange={handleInputChange}
					></textarea>
					<small id="emailHelp" className="form-text text-muted">
						Additional Information
					</small>
				</div>

				<button type="submit" className="btn btn-outline-primary btn-block">
					<i className="far fa-save"></i>
					<span> Save</span>
				</button>
			</form>
		</Modal>
	)
}
