import moment from 'moment'

export const stringFormatDateToDate = events => {
	if (Array.isArray(events)) {
		return events.map(e => ({
			...e,
			startDate: moment(e.startDate).toDate(),
			endDate: moment(e.endDate).toDate()
		}))
	}

	if (typeof events === 'object') {
		return {
			...events,
			startDate: moment(events.startDate).toDate(),
			endDate: moment(events.endDate).toDate()
		}
	}
}
