import { useDispatch, useSelector } from 'react-redux'
import { Calendar, momentLocalizer } from 'react-big-calendar'
import React, { useEffect, useState } from 'react'
import moment from 'moment'

import { openModalAction } from '../redux/actions/uiActions'
import {
	setActiveEventAction,
	startLoadEvents
} from '../redux/actions/eventsActions'

import NewEventButton from '../components/ui/NewEventButton'
import NavBar from '../components/ui/NavBar'
import DeleteEventButton from '../components/ui/DeleteEventButton'
import ToolBar from '../components/calendar/ToolBar'
import CalendarModal from '../components/calendar/CalendarModal'
import CalendarEvent from '../components/calendar/CalendarEvent'

import '../styles/screens/CalendarScreen.css'
import '../styles/components/ui/newEventButton.css'
import 'react-big-calendar/lib/css/react-big-calendar.css'
import '../styles/components/ui/deleteEventButton.css'
import 'react-big-calendar/lib/addons/dragAndDrop/styles.scss'

const localizer = momentLocalizer(moment)

export default function CalendarScreen() {
	const dispatch = useDispatch()
	const { events, activeEvent } = useSelector(state => state.events)

	useEffect(() => {
		dispatch(startLoadEvents())
	}, [dispatch])

	const [lastView, setLastView] = useState(
		localStorage.getItem('lastView') || 'week'
	)

	const selectHandler = e => dispatch(setActiveEventAction(e))

	const selectSlotHandler = e => {
		console.log('paso')
		dispatch(setActiveEventAction(null))
		e.action === 'doubleClick' && dispatch(openModalAction())
	}

	const dobleClickHandler = () => dispatch(openModalAction())

	const onViewChangeHandler = e => {
		setLastView(lastView)
		localStorage.setItem('lastView', e)
	}

	const eventStyleGetter = (event, start, end, isSelected) => {
		const defaults = {
			display: 'block',
			backgroundColor: '#367CF7',
			borderRadius: '5px',
			opacity: '0.8',
			color: 'white',
			outline: 'none'
		}

		return isSelected
			? {
					style: {
						...defaults,
						outline: '2px auto #0044bd'
					}
			  }
			: {
					style: {
						...defaults
					}
			  }
	}

	return (
		<div className="calendar__root">
			<div className="calendar__wrapper">
				<NavBar />
				<Calendar
					localizer={localizer}
					components={{
						event: CalendarEvent,
						toolbar: ToolBar
					}}
					defaultView={lastView}
					eventPropGetter={eventStyleGetter}
					events={events}
					onDoubleClickEvent={dobleClickHandler}
					onSelectEvent={selectHandler}
					onSelectSlot={selectSlotHandler}
					onView={onViewChangeHandler}
					selected={activeEvent}
					popup={true}
					startAccessor="startDate"
					endAccessor="endDate"
					selectable="ignoreEvents"
				/>
			</div>

			<NewEventButton />
			{activeEvent && <DeleteEventButton />}

			<CalendarModal />
		</div>
	)
}
